@mixin icon-next {
	font-family:FontAwesome;
	color:$color-body;
	content:$icon-chevron-right;
}
@mixin icon-previous {
	font-family:FontAwesome;
	color:$color-body;
	content:$icon-chevron-left;
}

@mixin icon($icon) {
  &:before {
	font-size:1em;
	color:inherit;
	font-family: FontAwesome;
	display:inline-block;
	text-decoration: inherit;
	line-height: 1;
	margin-right:.25em;
	content: $icon;
	font-weight:normal !important;
  }
}

.icon, .fa {
	&:before {
		font-weight:normal !important;
	}
}

@mixin icon-after($icon) {
  &:after {
	font-size:1em;
	font-weight:normal !important;
	color:inherit;
	font-family: FontAwesome;
	display:inline-block;
	text-decoration: inherit;
	line-height: 1;
	margin-left:.5em;
	content: $icon;

  }
}
.icon {
	&:before {
		font-size:1em;
		color:inherit;
		font-family: FontAwesome;
		display:inline-block;
		text-decoration: inherit;
		line-height: 1;
		margin-right:.25em;
  }
}
